/* */
'use strict';

class MediaDirtyFormDirController {

  /*@ngInject*/
  constructor() {
    this._getForm();
  }

  _getForm(){
    return this.length;
  }

}

export default class MediaDirtyFormDirective {

  constructor() {
    this.restrict = 'A';
    this.scope = {};
    this.bindToController = {
      name: '='
    };

    this.controller = MediaDirtyFormDirController;
    this.controllerAs = 'ctrl';
  }

  static directiveFactory() {
    var instance = new MediaDirtyFormDirective();
    return instance;
  }

}
